import Icon from './Components/Icons'
import Header from './Components/Header';
import Footer from './Components/Footer';
// import github from './linkedin.png'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import 'bootstrap/dist/css/bootstrap.css';
import './App.css';
import FaInstagram from 'react-icons/fa'
import { Card, CardBody, Container, Button, Col, Row } from "reactstrap"
import { useState } from 'react';

const itemArray = new Array(9).fill("empty")

function App() {

  const [isCross, setIsCross] = useState(false)
  const [reloadStatus, setReloadStatus] = useState(false)
  const [winMessage, setWinMessage] = useState("")
  const [reloadMessage, setReloadMessage] = useState("")
  const [circleWins, setCircle] = useState(0)
  const [crossWins, setCross] = useState(0)
  const [tied, setTied] = useState(0)
  const [ft, setFt] = useState(true)

  const reloadGame = () => {

    setFt(false)
    setIsCross(false)
    setReloadStatus(false)
    setWinMessage("")
    setReloadMessage("")
    itemArray.fill("empty", 0, 9)

  }

  const checkIsWinner = () => {
    if (itemArray[0] === itemArray[1] && itemArray[0] === itemArray[2] && itemArray[0] !== "empty") {
      setWinMessage(`${itemArray[0]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[0] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[3] === itemArray[4] && itemArray[3] === itemArray[5] && itemArray[3] !== "empty") {
      setWinMessage(`${itemArray[3]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[3] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[6] === itemArray[7] && itemArray[6] === itemArray[8] && itemArray[6] !== "empty") {
      setWinMessage(`${itemArray[6]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[6] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[0] === itemArray[3] && itemArray[0] === itemArray[6] && itemArray[0] !== "empty") {
      setWinMessage(`${itemArray[0]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[0] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[1] === itemArray[4] && itemArray[1] === itemArray[7] && itemArray[1] !== "empty") {
      setWinMessage(`${itemArray[1]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[1] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[2] === itemArray[5] && itemArray[2] === itemArray[8] && itemArray[2] !== "empty") {
      setWinMessage(`${itemArray[2]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[2] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[0] === itemArray[4] && itemArray[0] === itemArray[8] && itemArray[0] !== "empty") {
      setWinMessage(`${itemArray[0]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[0] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    } else if (itemArray[2] === itemArray[4] && itemArray[2] === itemArray[6] && itemArray[2] !== "empty") {
      setWinMessage(`${itemArray[2]} wins`)
      reloadStatus ? setTied(tied + 1) : itemArray[2] === "circle" ? setCircle(circleWins + 1) : setCross(crossWins + 1);
    }
  }

  const changeItem = itemNumber => {
    if (winMessage) {
      return toast(winMessage, { type: "success" });
    }

    if (itemArray[itemNumber] === "empty") {
      itemArray[itemNumber] = isCross ? "cross" : "circle"
      setIsCross(!isCross)
    } else {
      return toast("Already Filled", { type: "error" })
    }
    checkReload();
    checkIsWinner();
  }

  const checkReload = () => {
    if (itemArray.includes("empty")) {
      setReloadStatus(false)
    } else {
      setReloadMessage("try again")
      setTied(tied + 1)
      setReloadStatus(true)
    }
  }
  return (
    <Container className="p-5">
      <Header />
      <ToastContainer position="bottom-center" />
      <Row>
        <Col md={6} className="offset-md-3">
          {
            reloadStatus ? (
              <div className="mb-2 mt-2">
                <h1 className="text-success text-uppercase text-center">{reloadMessage}</h1>
                <Button color="success" block onClick={reloadGame} >Reload the Game </Button>
              </div>
            ) :
              winMessage ? (
                <div className="mb-2 mt-2">
                  <h1 className="text-success text-uppercase text-center">{winMessage}</h1>
                  <Button color="success" block onClick={reloadGame} >Reload the Game </Button>
                </div>

              ) : (
                <h1 className="text-center text-white" style={{ marginBottom: '5%' }}>
                  { isCross ? "Cross turns" : "Circle turns"}
                </h1>
              )
          }
          <div className="grid">
            {
              itemArray.map((item, index) => (
                <Card color="warning" onClick={() => changeItem(index)}>
                  <CardBody className="box" >
                    <Icon name={item} />
                  </CardBody>
                </Card>
              ))
            }
          </div>
        </Col>
      </Row >
      <p className="text-center text-white">
        <span> Circle : {circleWins} | </span>
        <span >Tied : {tied} | </span>
        <span>Cross : {crossWins} </span>
      </p>
      <Footer />
    </Container >
  );
}

export default App;
